import { Button, Input, notification } from 'antd';
import React, { useState } from 'react';
import { useSetRecoilState } from 'recoil';
import { todolistState } from '../todolistState';

// ç”¨äºåˆ›å»ºå”¯ä¸€ ID çš„å·¥å…·
let id = 0;
function getId() {
  return id++;
}

const Add = () => {
  const [value, setValue] = useState<string>('');
  const setRecords = useSetRecoilState(todolistState);
  const addAction = () => {
    if (!value) {
      notification.error({ message: 'è¯·è¾“å…¥æœ‰æ•ˆå€¼' });
    } else {
      setRecords((prev) => [{ id: getId(), isComplete: false, text: value }, ...prev]);
      setValue('');
    }
  };

  return (
    <div style={{ display: 'flex' }}>
      <Input onChange={(e) => setValue(e.target.value)} value={value} />
      <Button type="primary" onClick={addAction}>
        æ·»åŠ 
      </Button>
    </div>
  );
};

export default Add;
